body {
    padding: 0;
    margin: 0;
}


.mainBody {
    font-family: "Helvetica Neue", Helvetica, sans-serif;
    max-width: 600px;
    margin: 0 auto; 
    padding: 1vw;
    
}

p, li {
    font-size: 1.1rem;
    line-height: 1.5em;
}

li {
    list-style: none; 
    margin: 0px !important;
    padding: 0px !important;
}

.projectTitle {
    font-weight: bold;
}

a{
    text-decoration: none; 
}

h1 {
    font-size: 40px; 
    text-align: center;
}

h2 {
    font-size: 30px;
}

/* .logo {
    
    
  } */
#douglas {
    height: 150px;
    width: 150px;
    float: right;
    margin: 0 0 0 10px;
    border-radius: 50%;
} 

.buttonMain {
    -webkit-border-radius: 30px;
    background: #242e3a;
    color: #FFF;
    padding: 20px 20px;
    margin: 20px;
}

.mainHeader {
    background-image: linear-gradient(90deg, #57cbf8 0%, #732173 60%, #ff00bf 100%);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-weight: 790;
}

.footer {
    --footer-background: #000000;
    --footer-text-color: #ffffff;
    background: var(--footer-background);
    color: var(--footer-text-color);
    min-width: 1024px;
    overflow: hidden;
    position: relative;
    z-index: 1;
    margin: 0rem -3rem -5rem -1rem;
    max-width: 100%;

  }

  .footers {
    position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   background-color: red;
   color: white;
   text-align: center;
 }

 .footerz {
    background:#080a2df6;
    color:#dedbdb;
    height: 5em;
    display: flex;
    padding: 0 8em;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-family: "Helvetica Neue", Helvetica, sans-serif;
 }
 